import React, {Component} from 'react';

class Navbar extends Component {
   constructor(props) {
      super(props);
      this.state = {};
   }
   render() {
      return (
      <nav className="navbar sticky-top navbar-expand-xl" style={{backgroundColor : '#edeceb', marginBottom: 5}}>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav" style={{marginLeft : 'auto', paddingRight : 10}}>
      <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" style={{marginRight : 10}} />
      <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
      </ul>
      </div>
      </nav>
      );
   }
}

export default Navbar;
