import React, {Component} from 'react';
import Card from './Card';
import './App.css';

let workspaceData = [];
let workspacesToShow = [];

class App extends Component {
  constructor(props) {
     super(props);
     this.state = {data : null};
     this.updateData = this.updateData.bind(this);
  }
  componentDidMount() {
    Promise.all([
	   fetch('https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=1-100&api_key=keyuFtFTBZMHrzjOn'),
	   fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=101-200&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=201-300&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=301-400&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=401-500&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=501-600&api_key=keyuFtFTBZMHrzjOn`),
     fetch(`https://api.airtable.com/v0/app6xReKkFDtzQ655/MATHia%20Workspaces?maxRecords=100&view=601-700&api_key=keyuFtFTBZMHrzjOn`)])
    .then(function (responses) {
	     return Promise.all(responses.map(function (response) {
		   return response.json();
	   }));
    }).then(function (data) {
      	workspaceData.push(...data[0].records, ...data[1].records, ...data[2].records, ...data[3].records,
                           ...data[4].records, ...data[5].records, ...data[6].records);
      });
    var that = this;
    setTimeout(function(){that.updateData();}, 1250);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.state.data !== nextState.data) return true;
  }
  updateData() {
    this.setState({data : workspaceData});
    workspaceData = [];
  }
  render() {
    if (workspaceData.length < 641) return <span>Loading...</span>;
    else {
    for (var a = 0; a < this.state.data.length; a++) {
    var newDescription = this.state.data[a].fields.Description.length > 125 ? this.state.data[a].fields.Description.slice(0, 125) + 'â€¦' : this.state.data[a].fields.Description;
    workspacesToShow.push(<Card workspace={this.state.data[a].fields.Workspace} description={newDescription} ccss={this.state.data[a].fields.CCSS} time={this.state.data[a].fields['Median Time']} sampleimg={this.state.data[a].fields.SampleImg} sbs={this.state.data[a].fields.SBS} sequence={this.state.data[a].fields['Course(s) and Sequences']} wksptype={this.state.data[a].fields['CT/NM']} skills={this.state.data[a].fields.Skills} key={a} />);
    }
    return (
    <div className="App">
    {workspacesToShow}
    </div>
    );
    }
  }
}

export default App;
